import React from 'react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faWhatsapp } from '@fortawesome/free-brands-svg-icons'
import { faEnvelope } from '@fortawesome/free-regular-svg-icons'
import { faTelegramPlane } from '@fortawesome/free-brands-svg-icons'
import { faInstagram } from '@fortawesome/free-brands-svg-icons'

import classNames from 'classnames';

import './Contacts.css';

function ContactItem(props) {
    const attrs = props.sameTab ?
                  {} :
                  { target: "_blank", rel: "noopener noreferrer"};
    return (
        <a
            {...attrs}
            title={props.title}
            href={props.href}
        >
            <FontAwesomeIcon icon={props.icon} />
        </a>
    );
}

function Contacts(props) {
    const containerClass = classNames(props.className, 'contacts-container');
    return (
        <div className={containerClass}>
            <ContactItem
                title="WhatsApp"
                href="https://api.whatsapp.com/send?phone=4917621986527"
                icon={faWhatsapp}
            />
            <ContactItem
                href="mailto://abaibik@gmail.com"
                title="Email"
                icon={faEnvelope}
                sameTab
            />
            <ContactItem
                title="Telegram"
                href="https://telegram.me/Tetatinang"
                icon={faTelegramPlane}
            />
            <ContactItem
                title="Instagram"
                href="https://www.instagram.com/depress_city/"
                icon={faInstagram}
            />
        </div>
    );
}

export default Contacts;
